  padding-left: 25px;
  color: #5f5f5f;
  line-height: 18px;
  cursor: pointer;
}

.check input {
  display: none;
}

.check:before {
  content: '';
  height: 50px;
  border: 2px solid #ccc;
  left: 0;
  top: 0;
  width: 18px;
  height: 18px;
