@media (min-width: 767px) {
  .title__main {
    font-size: calc(20px + 10 * (100vw / 1280));
  }

  .info-main-screen__title {
    font-size: calc(35px + 22 * (100vw / 1280));
  }

  .text-info-box__title {
    font-size: calc(15px + 3 * (100vw / 1280));
  }
}

@media (max-width: 767.98px) {
  body.lock {
    width: 100%;
    position: fixed;
    overflow: hidden;
  }

  .form__info {
    width: 50px;
    height: 50px;
    line-height: 50px;
  }

