    margin: 0px 15px 0px 0px;
  }

  .main-screen {
    background: url("../../dist/img/main-screen-bg.jpg") 45% no-repeat;
  }
}

@media (max-width: 767px) {
  .title__main {
    font-size: calc(20px + 17 * ((100vw - 320px) / 1280));
  }

  .info-main-screen__title {
    font-size: calc(35px + 37.4 * ((100vw - 320px) / 1280));
  }

  .text-info-box__title {
    font-size: calc(15px + 5.1 * ((100vw - 320px) / 1280));
  }
}

